/*
 UNICO TEMPLATE THEME VARIABLES
*/

:root,
[data-theme="theme-default-light"] {
    /* MAIN */
    --main-mid-color: #1b1b1d;
    --main-font-color: #807594;
    --main-default-button-color: #da4389; 
    --main-logo-font-color: #baaad8;
    --main-logo-img-brightness: brightness(2.5);
    --main-logo-bg-color: #713bdb;
    --main-logo-border-color: #4646464d;
    --main-scroll-color: #595959;
    --main-graph-line-color: #34343480;
    --main-graph-font-color: #91939d; 
    --main-notice-color: #2f70e9;
    --main-success-color: #61d688;
    --main-warning-color: #e9cd53;
    --main-danger-color: #e74444;
    --main-transparent-color: #00000000;
    --main-inside-color: #17142045;
    
    /* SIDEBAR */
    --sidebar-bg-color: #2b2741;
    --sidebar-info-card-color: #180d38;
    --sidebar-gradient-color: linear-gradient(270deg, #2d1d3b2e, transparent);
    --sidebar-shadow-color: #17171780;
    --sidebar-hover-color: #d286ea33;

    /* header */
    --header-bg-color: #ffffff;
    --header-border-color: #eceaf1;
    --header-shadow-color: #e1dfea;

    /* CONTENTS */
    --content-bg-color: #f1f1f7;
    --content-border-color: #f6f3f97a;
    --content-card-color: #ffffff;
    --content-card-header-color: #00000005;
    --content-card-shadow-color: #9aa1ab26;
    --content-font-color: #807594;
    --content-font-active-color: #d1cbe0;
    --content-font-disable-color: #58497180;
    --content-input-bg-color: #f1f1f7;
    --content-input-hover-color: #d8c4d033;
    --content-example-color: #f1f1f7;
    --content-table-odd: #f1f1f77a;

    --footer-bg-color: #ffffff;

    --scrollbar-color: #88818db3;
    --scrollbar-rail-color: #dfcfe466;

}

[data-theme="theme-default-dark"] {
    /* MAIN */
    --main-mid-color: #ded9eb;
    --main-font-color: #807594;
    --main-default-button-color: #da4389;
    --main-logo-font-color: #baaad8;
    --main-logo-img-brightness: brightness(2.5);
    --main-logo-bg-color: #713bdb;
    --main-logo-border-color: #4646464d;
    --main-scroll-color: #595959d1;
    --main-graph-line-color: #17171780;
    --main-graph-font-color: #a19bab;
    --main-notice-color: #2f70e9;
    --main-success-color: #61d688;
    --main-warning-color: #e9cd53;
    --main-danger-color: #e74444;
    --main-transparent-color: #00000000;
    --main-inside-color: #17142045;

    /* SIDEBAR */
    --sidebar-bg-color: #2b2741;
    --sidebar-info-card-color: #180d38;
    --sidebar-gradient-color: linear-gradient(270deg, #2d1d3b2e, transparent);
    --sidebar-shadow-color: #17171780;
    --sidebar-hover-color: #d286ea33;

    /* header */
    --header-bg-color: #2b2741;
    --header-border-color: #242b46;
    --header-shadow-color: #19181f;
    
    /* CONTENTS */
    --content-bg-color: #262138;
    --content-border-color: #2f2a464d;
    --content-card-color: #2b2741;
    --content-card-header-color: #bfaaff0a;
    --content-card-shadow-color: #0000004d;
    --content-font-color: #807594;
    --content-font-active-color: #d1cbe0;
    --content-font-disable-color: #75668e80;
    --content-input-bg-color: #262138;
    --content-input-hover-color: #68529233;
    --content-example-color: #262138;
    --content-table-odd: #2621387a;

    --footer-bg-color: #2b2741;
    
    --scrollbar-color: #423757;
    --scrollbar-rail-color: #211e33;

} 
